  methods: {
    async downloadFile() {
            const result = await axios.get(
            'http://127.0.0.1:5000/downloadPdf'
            )
            const base64pdfdata= result.data.body.replace(/"/g , '')
            const contentType = 'application/pdf'
            const sliceSize = 512;
            const blobData = this.b64toBlobMethod(base64pdfdata, contentType, sliceSize) 
            var FILE = window.URL.createObjectURL(new Blob([blobData]));
            var docUrl = document.createElement('a');
            docUrl.href = FILE;
            docUrl.setAttribute('download', 'destination.pdf');
            document.body.appendChild(docUrl);
            docUrl.click();
    },
    b64toBlobMethod(b64Data, contentType, sliceSize) {
      contentType = contentType || '';
      sliceSize = sliceSize || 512;
      var byteCharacters = atob(b64Data);
      var byteArrays = [];
      for (var offset = 0; offset < byteCharacters.length; offset += sliceSize) {
      var slice = byteCharacters.slice(offset, offset + sliceSize);
      var byteNumbers = new Array(slice.length);
      for (var i = 0; i < slice.length; i++) {
        byteNumbers[i] = slice.charCodeAt(i);
      }
      var byteArray = new Uint8Array(byteNumbers);
      byteArrays.push(byteArray);
    }
    var blob = new Blob(byteArrays, {type: contentType});
    return blob;
  },



